article{
    position: relative;
    padding-top: 50px;
    left: 40%;
    width: 60%;
    animation: rise 1s ease-in-out 250ms backwards;
}
.title{
    float: left;

    h3{
        position: relative;
        font-size: 25px;
        color: var(--title-color);
    }
}
.desc{
    float: left;
    background-color: var(--body-bg-color);

    p{
        position: relative;
        width: 65%;
        line-height: 30px;
        word-wrap: break-word;
        font-size: 18px;
        font-weight: 400;
        color: var(--p-color);
    }
    img{
        cursor: pointer;
        position: relative;
        margin-right: 50px;
        margin-top: 50px;
        margin-bottom: 50px;
        width: 100px;
        height: 100px;
        user-select: none;
    }
    .skills, table{
        position: relative;
        width: 70%;
        height: 100%;
        border-collapse: collapse;
    
        .row, tr{
            display: flex;
            position: relative;
            margin-bottom: 50px;
            width: 100%;
    
            .col, td{
                position: relative;
                width: 50%;
                text-align: left;
                line-height: 25px;
    
                h3{
                    display: inline;
                    text-align: left;
                    text-transform: uppercase;
                    letter-spacing: 0.5px;
                    font-size: 16px;
                    font-weight: 500;
                    color: var(--title-color);
                }
                .bar{
                    position: relative;
                    width: 70%;
                    height: 6px;
                    background: var(--border-color);
    
                    .fill{
                        position: absolute;
                        top: 0;
                        left: 0;
                        height: 6px;
                        background: var(--highlight-color);
                        animation: barFill 0.5s ease-in;
                    }
                }
            }
        }
        .row:first-child,
        tr:first-child{
            margin-top: 25px;
        }
        .row:last-child,
        tr:last-child{
            margin-bottom: 25px;
        }
    }
    .school{
        position: relative;
        margin-bottom: 100px;

        &:first-child{
            margin-top: 25px;
        }
        h2{
            position: relative;
            text-align: left;
            font-size: 16px;
            color: var(--title-color);
        }
        label[name="time"],
        label[name="place"],
        label[name="type"]{
            position: relative;
            text-align: left;
            line-height: 25px;
            font-size: 10px;
            color: var(--p-color);
        }
        label[name="type"]{
            line-height: 30px;
            font-size: 14px;
            color: var(--title-color);
        }
        #desc{
            display: inline-block;
            position: absolute;
            top: 0;
            left: 35%;
            width: 25%;
            word-wrap: break-word;
            line-height: 25px;
            font-size: 15px;
        }
    }
}

@media screen and(max-width: 1650px){
    .desc{
        .skills, table{
            width: 80%;
        }
        .school #desc{
            left: 40%;
            width: 30%;
        }
    }
}
@media screen and(max-width: 1460px){
    article{
        width: 800px;
    }
    .desc{
        p{
            width: 70%;
        }
        .skills .row .col .bar,
        table tr td .bar{
            width: 75%;
            height: 5px;

            .fill{
                height: 5px;
            }
        }
        .school #desc{
            width: 34%;
        }
    }
}
@media screen and(max-width: 1300px){
    article{
        left: 45%;
        width: 750px;
    }
    .title h3{
        font-size: 22px;
    }
    .desc{
        p{
            font-size: 16px;
        }
        .skills .row .col h3,
        table tr td h3{
            font-size: 15px;
        }
        .school{
            h2{
                font-size: 14px;
            }
            label[name="type"]{
                font-size: 12px;
            }
            #desc{
                font-size: 13px;
            }
        }
    }
}
@media screen and(max-width: 1150px){
    article{
        left: 0;
        width: 100%;
    }
    .title{
        padding: 20px !important;
        height: 50px;
        text-align: left;
        line-height: 10px;
        border-bottom: 1px solid var(--border-color);
        background: var(--title-bg-color);

        h3{
            left: 5%;
            text-transform: uppercase;
            font-size: 18px;
        }
    }
    .desc{
        padding: 20px;
        border-bottom: 1px solid var(--border-color);

        p{
            left: 5%;
            padding-top: 20px;
            padding-bottom: 20px;
            width: 90%;
            line-height: 25px;
            font-size: 15px;
        }
        img{
            left: 5%;
        }
        .skills, table{
            left: 5%;
            width: 100%;
            height: 100%;

            .row .col .bar,
            tr td .bar{
                width: 80%;
            }
        }
        table{
            margin-bottom: 0;
        }
        .school{
            h2{
                left: 5%;
                font-size: 13px;
    
                top: 20px;
            }
            label[name="time"],
            label[name="place"],
            label[name="type"]{
                top: 20px;
                left: 5%;
            }
            #desc{
                left: 55%;
                width: 40%;
                font-size: 12px;
            }
        }
    }
}
@media screen and(max-width: 550px){
    .title h3{
        font-size: 15px;
    }
    .desc{
        p{
            font-size: 12px;
        }
        .skills .row{
            display: initial;
            margin-bottom: 0;

            .col{
                margin-bottom: 50px;
                width: 100%;

                &:first-child{
                    margin-top: 50px;
                }
                .bar{
                    width: 90%;
                }
            }
        }
        .skills .row .col h3,
        table tr td h3{
            font-size: 13px;
        }
        .school{
            margin-bottom: 50px;
    
            h2{
                font-size: 12px;
            }
            label[name="time"],
            label[name="place"],
            label[name="type"]{
                left: 5%;
                line-height: 20px;
                font-size: 9px;
            }
            label[name="type"]{
                line-height: 20px;
                font-size: 11px;
            }
            #desc{
                display: block;
                position: relative;
                left: 5%;
                margin-top: 5px;
                width: 90%;
                line-height: 20px;
                font-size: 11px;
            }
        }
    }
}