.mode-toggle{
    cursor: pointer;
    position: fixed;
    top: 25px;
    right: 25px;
    z-index: 15;
    width: 45px;
    height: 25px;
    overflow: visible;
    border: 2px solid var(--p-color);
    border-radius: 20px;
    background: var(--body-bg-color);
    user-select: none; 
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;

    .circle{
        cursor: pointer;
        display: table;
        position: absolute;
        top: -5px;
        left: -5px;
        z-index: 15;
        width: 32px;
        height: 32px;
        text-align: center;
        white-space: nowrap;
        border-radius: 50%;
        background: var(--modetogglecircle-bg-color);
        transition: transform 0.25s ease-in-out;

        .fas.fa-moon{
            display: table-cell;
            vertical-align: middle;
            text-align: center;
            color: var(--modetogglemoon-color);
        }
    }
}
.mode-dark{
    background: var(--body-color);
}
.circle-dark{
    transform: translateX(20px);
    background: var(--circle-background);
}

@media screen and(max-width: 1150px){
    .mode-toggle{
        position: absolute;
        top: 15px;
        right: 15px;
    }
}
@media screen and(max-width: 550px){
    .mode-toggle{
        top: 20px;
        width: 30px;
        height: 15px;

        .circle{
            width: 22px;
            height: 22px;
    
            .fas.fa-moon{
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                margin: auto;
                font-size: 11px;
            }
        }
    }
    .circle-dark{
        transform: translateX(15px);
    }
}