.backgroundBlur{
    filter: blur(2px);
    height: 100%; 
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.image-view{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 15;
    width: 100%;
    height: 100%;
    background: rgba(25, 25, 25, 0.5);
    animation: appear 0.5s;

    .load-ring{
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 50px;
        height: 50px;

        &::after{
            display: block;
            margin: 0;
            width: 50px;
            height: 50px;
            content: " ";
            border: 5px solid var(--highlight-color);
            border-radius: 50%;
            border-color: var(--highlight-color) transparent var(--highlight-color) transparent;
            animation: load-ring 1s linear infinite;
        }
    }
    .close{
        cursor: pointer;
        position: absolute;
        top: 20px;
        right: 20px;
        width: 40px;
        height: 40px;
        text-align: center;
        font-size: 25px;
        font-weight: bolder;
    
        p{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: 40px;
            color: var(--border-color);
        
            &:hover{
                color: #ffffff;
            }
        }
    }
    .image{
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: auto;
        max-width: 80%;
        height: auto;
        border: 2px solid var(--border-color);
        border-radius: 5px;
        user-select: none;
    }
    .ArrowLeft,
    .ArrowRight{
        cursor: pointer;
        position: absolute;
        top: 20%;
        width: 10%;
        height: 60%;
        text-align: center;
        transition: all 0.25s ease;

        &:hover{
            background: rgba(0, 0, 0, 0.5);
        }
        &:hover > .arrow{
            border: solid #ffffff;
            border-width: 0 10px 10px 0;
        }
        .arrow{
            cursor: pointer;
            display: inline-block;
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            padding: 15px;
            border: solid var(--border-color);
            border-width: 0 10px 10px 0;
            transition: border 0.25s ease;
        }
        .left{
            transform: rotate(135deg);
        }
        .right{
            transform: rotate(-45deg);
        }
        #leftIndex,
        #rightIndex{
            display: none;
        }
    }
    .ArrowLeft{
        left: 0;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
    }
    .ArrowRight{
        right: 0;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }
}
.pf-desc{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80px;
    text-align: center;
    
    p{
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        letter-spacing: 0.5px;
        font-size: 20px;
        font-weight: 100;
        color: #ffffff;
    }
}