@import "./animations";
@import "./mode-toggle";
@import "./image-view";
@import "./nav";
@import "./article";
@import "./footer";

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: -apple-system, system-ui, BlinkMacSystemFont, 
    "Segoe UI", Roboto, "Helvetica Neue", 
    Arial, sans-serif;
}
html, body{
    overflow-x: hidden;
    scroll-behavior: smooth;

    a{
        cursor: pointer;
        text-decoration: none;
        color: inherit;
    
        &:hover{
            text-decoration: underline;
        }
    }
}
body{
    --body-bg-color: #ffffff;
    --title-bg-color: #fafafa;
    --title-color: #737373;
    --modetogglecircle-bg-color: #0a0f14;
    --modetogglemoon-color: #ffc800;
    --highlight-color: var(--modetogglemoon-color);
    --p-color: #969696;
    --border-color: #dcdcdc;
    background: var(--body-bg-color);

    &.dark{
        --body-bg-color: #0a0f14;
        --title-bg-color: #0f1419;
        --title-color: #c8d2dc;
        --modetogglecircle-bg-color: #c8b400;
        --modetogglemoon-color: #32323c;
        --highlight-color: var(--modetogglecircle-bg-color);
        --p-color: #8c96a0;
        --border-color: #32323c;
    }
    .container{
        min-height: 100vh;
    }
    .wrapper{
        padding-bottom: 90px;
    }
}