footer{
    position: relative;
    padding: 20px;
    padding-left: 100px;
    width: 100%;
    height: 90px;
    clear: both;
    border-top: 1px solid var(--border-color);
    background: var(--title-bg-color);

    span li{
        position: relative;
        list-style-type: none;
        letter-spacing: 0.5px;
        line-height: 25px;
        font-size: 13px;
        font-weight: 300;
        color: var(--p-color);

        strong{
            text-transform: uppercase;
            letter-spacing: 0;
            font-size: 14px;
        }
    }
}

@media screen and (max-width: 1650px){
    footer{
        padding-left: 50px;
    }
}
@media screen and (max-width: 1460px){
    footer span li{
        line-height: 25px;
        font-size: 12px;

        strong{
            font-size: 13px;
        }
    }
}
@media screen and (max-width: 1150px){
    footer{
        padding-left: 0;
        padding: 20px;

        span li{
            left: 5%;
        }
    }
}