.ghost-nav{
    position: relative;
    top: 0;
    left: 0;
    margin: 0;
    width: 100%;
    height: 100%;
}
.sticky-nav{
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    padding-top: 20px;
    width: 100%;
    height: 55px;
    background-color: var(--title-bg-color);
}
.sticky-img{
    cursor: pointer;
    position: fixed;
    top: 10px;
    left: 10px;
    width: 35px;
    height: 35px;
}
.sticky-icon{
    position: fixed;
    top: 20px;
    left: 5px;
    margin-top: 0;
    margin: auto;
}
.sticky-hidden{
    visibility: hidden;
}
nav{
    position: fixed;
    padding: 100px;
    width: 666px;
    height: 100%;
    font-size: 35px;
    background-color: var(--body-bg-color);

    img{
        position: relative;
        float: left;
        width: 200px;
        height: 200px;
        border: 2px solid var(--border-color);
        border-radius: 50%;
        user-select: none;
        animation: rise 1s ease-in-out 250ms backwards;
    }
    h3{
        position: relative;
        top: 50px;
        left: 50px;
        text-align: left;
        letter-spacing: .5px;
        line-height: 35px;
        font-size: 30px;
        font-weight: 300;
        color: var(--p-color);
        animation: rise 1s ease-in-out 500ms backwards;

        mark{
            position: inherit;
            text-transform: uppercase;
            font-size: 35px;
            font-weight: 600;
            color: var(--highlight-color);
            background-color: transparent;
            animation: rise 1s ease-in-out 750ms backwards
        }
    }
    p{
        position: relative;
        top: 50px;
        left: 50px;
        line-height: 50px;
        letter-spacing: 0.5px;
        word-wrap: break-word;
        font-size: 15px;
        font-weight: 300;
        color: var(--p-color);
        animation: rise 1s ease-out 1s backwards;
    }
    i{
        position: relative;
        left: 30px;
        margin-top: 60px;
        margin-left: 20px;
        margin-right: 20px;
        width: 38px;
        font-size: 15px;
        color: var(--title-color);

        &:hover{
            color: var(--highlight-color);
        }
    }
    .info{
        position: relative;
        top: 40px;
        padding-top: 10px;
        width: 100%;
        height: 75px;
        text-align: center;
        border-top: 2px solid var(--border-color);
        animation: rise 1s ease-out 1.5s backwards;

        .row{
            display: inline-block;
            position: absolute;

            .col{
                position: relative;
                margin-bottom: 20px;
                text-align: left;
                font-size: 12px;
            }
            .col:first-child{
                color: var(--title-color);
                font-weight: bold;
            }
            .col:last-child{
                color: var(--p-color);
            }
        }
        .row:first-child{
            left: 0;
            animation: rise 750ms ease-out 1.5s backwards;
        }
        .row:last-child{
            right: 0;
            animation: rise 750ms ease-out 1.75s backwards;
        }
    }
}

@media screen and(max-width: 1650px){
    nav{
        padding: 50px;

        .info{
            width: 82%;
        }
    }
}
@media screen and(max-width: 1460px){
    nav{
        font-size: 30px;
        width: 600px;

        img{
            width: 180px;
            height: 180px;
        }
        h3{
            top: 45px;
            left: 45px;
            line-height: 30px;
            font-size: 25px;

            mark{
                font-size: 30px;
            }
        }
        p{
            top: 45px;
            left: 45px;
            font-size: 12px;
        }
        i{
            left: 35px;
            margin-top: 50px;
            margin-left: 10px;
            margin-right: 10px;
            width: 42px;
        }
    }
}
@media screen and (max-width: 1150px){
    nav{
        position: relative;
        padding: 20px;
        padding-top: 50px;
        width: 100%;
        height: 500px;
        text-align: center;
        border-bottom: 1px solid var(--border-color);

        img{
            display: block;
            float: unset;
            margin: auto;
            width: 150px;
            height: 150px;
        }
        h3{
            top: 25px;
            left: 0;
            margin: auto;
            width: unset;
            text-align: center;
            font-size: 25px;

            mark{
                font-size: 30px;
            }
        }
        p{
            top: 25px;
            left: 0;
            margin: auto;
        }
        i{
            left: 0;
            margin-top: 40px;
            margin-left: 20px;
            margin-right: 20px;
            font-size: 18px;
        }
        .info{
            left: 5%;
            width: 90%;
        }
    }
}
@media screen and (max-width: 550px){
    nav{
        min-height: 550px;
        height: auto;

        img{
            width: 100px;
            height: 100px;
        }
        h3{
            top: 10px;
            font-size: 20px;
            line-height: 23px;

            mark{
                font-size: 22px;
            }
        }
        p{
            top: 10px;
        }
        i{
            font-size: 15px;
            margin-left: 10px;
            margin-right: 10px;
        }
        .info{
            padding-top: 25px;
            height: auto;
    
            .row{
                display: block;
                position: relative;
                padding-bottom: 25px;
                width: 100%;
    
                .col{
                    text-align: center;
                }
            }
        }
    }
}